import React, { useEffect, useState } from "react";
import { Grid, Column } from "@carbon/react";
import CustomDataTable from "./CustomDataTable";
import axios from "axios";
import XMLParser from "react-xml-parser";

const CollectionByWeight = (props) => {
  const [bggData, setBggData] = useState([]);

  const tableHeaders = [
    { key: "Game", header: "Game" },
    { key: "Weight", header: "Weight" },
  ];

  useEffect(() => {
    async function getWeight() {
       const collection = [];
    const collectionResponse = await axios.get(
      `https://api.geekdo.com/xmlapi2/collection?username=${props.user}&subtype=boardgame&own=1&excludesubtype=boardgameexpansion`
    );
    const collectionParsedData = new XMLParser().parseFromString(collectionResponse.data);
    collectionParsedData.children.map(collItem => {
        collection.push({GameId: collItem.attributes.objectid, Game: collItem.children.find(collItemC => collItemC.name === "name").value})
    })



      let uniqueGamesArray = collection.reduce((prev, curr) => {
        const exists = prev.find((g) => g.Game === curr.Game);
        if (!exists) {
          prev.push({ Game: curr.Game, GameId: curr.GameId });
        }
        return prev;
      }, []);

      const ids = uniqueGamesArray.map((g) => g.GameId);

      const response = await axios.get(
        `https://boardgamegeek.com/xmlapi2/thing?id=${ids}&stats=1`
      );
      const parsedData = new XMLParser().parseFromString(response.data);

      const toReturn = [];
      parsedData.children.map((c) => {
        const statistics = c.children.find((a) => a.name === "statistics");
        if (statistics) {
          statistics.children.map((c2) => {
            const weight = c2.children.find(
              (a2) => a2.name === "averageweight"
            );
            if (weight) {
              const game = uniqueGamesArray.find(
                (u) => u.GameId === c.attributes.id
              );
              if (game) {
                toReturn.push({
                  ...game,
                  id: game.GameId,
                  Weight: parseFloat(weight.attributes.value).toFixed(2),
                });
              }
            }
          });
        }
      });
      setBggData(toReturn);
    }
    getWeight();
  }, []);

  return (
    <Grid condensed>
      <Column lg={16} md={8} sm={4}>
        <CustomDataTable
          data={bggData}
          headers={tableHeaders}
          title="Collection by weight"
          sortable={true}
        />
      </Column>
    </Grid>
  );
};

export default CollectionByWeight;
